.review {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .review {
    width: 768px;
    margin: 0 auto;
    padding-top: 32px;
  }
}
@media (min-width: 1280px) {
  .review {
    width: 1280px;
    margin: 0 auto;
    padding-top: 83px;
  }
}
.review__header {
  width: 282px;
  margin: 0 auto;
  padding-bottom: 28px;
}
.review__header .review__title {
  width: 282px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 35px;
  font-size: 18px;
}
.review__header .review__text-reasons {
  width: 282px;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .review__header {
    width: 435px;
    margin: 0 auto;
    margin-bottom: 102px;
    padding-bottom: 0;
  }
  .review__header .review__title {
    width: 317px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
  }
  .review__header .review__text-reasons {
    width: 435px;
    margin: 0 auto;
    text-align: center;
  }
}
@media (min-width: 1280px) {
  .review__header {
    width: 483px;
    margin: 0 auto;
    margin-bottom: 66px;
  }
  .review__header .review__title {
    width: 483px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 35px;
    font-size: 30px;
  }
  .review__header .review__text-reasons {
    width: 483px;
    margin: 0 auto;
    text-align: center;
  }
}
